/* COLORS */
body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #050505;
  background-color: #fafafa; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  text-transform: uppercase; }

a {
  color: #050505;
  text-decoration: none; }
  a:active, a:hover {
    color: #c82525; }
